"use client";

import { useRouter } from 'next/navigation';
import { useState } from 'react';
import socket from '../socket';

const HomePage = () => {
    const router = useRouter();
    const [roomId, setRoomId] = useState('');
    const [username, setUsername] = useState('');

    const createRoom = () => {
        const newRoomId = Math.random().toString(36).substring(7);  // Générer un ID unique pour la room
        router.push(`/room/${newRoomId}`);  // Redirige vers la nouvelle room
    };

    const joinRoom = () => {
        if (roomId.trim()) {
            socket.emit('joinRoom', { roomId, username: 'Player' });  // Envoie l'ID de la room et le nom d'utilisateur
            router.push(`/room/${roomId}`);
        }
    };

    return (
        <div className="p-4">
            <h1>Bienvenue au Quiz Game</h1>
            <button onClick={createRoom} className="bg-blue-500 hover:bg-blue-600 text-white p-2 rounded-md">
                Créer une nouvelle room
            </button>

            <div className="mt-4">
                <input
                    type="text"
                    placeholder="ID de la room"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    className="p-2 border rounded-md"
                />
            </div>

            <div className="mt-4">
                <input
                    type="text"
                    placeholder="ID de la room"
                    value={roomId}
                    onChange={(e) => setRoomId(e.target.value)}
                    className="p-2 border rounded-md"
                />
                <button onClick={joinRoom} className="bg-green-500 hover:bg-green-600 text-white p-2 rounded-md ml-2">
                    Rejoindre la room
                </button>
            </div>
        </div>
    );
};

export default HomePage;
